import React from 'react'
import { Switch, Route } from 'react-router-dom';
import { PortfolioApp } from './pages/PortfolioApp.jsx';
import {Nav} from './cmps/Nav.jsx'

function App() {
    return (
        <div>
            <Nav />
            <Switch>
                <Route component={PortfolioApp} path='/' />
            </Switch>
        </div>
    )
}
export default App
