import React, { Component } from 'react'
import Appsus from '../assets/imgs/appsus.gif';
import Meme from '../assets/imgs/meme.gif';
import MakeEat from '../assets/imgs/makeeat.gif';
import VisibilityIcon from '@material-ui/icons/Visibility';
import CodeIcon from '@material-ui/icons/Code';
import Zoom from '@material-ui/core/Zoom';
import Tooltip from '@material-ui/core/Tooltip';

export class Proj extends Component {
    render() {
        return (
            <div className="proj main-container flex" id="proj">
                <h2 className="proj-title" >My Projects</h2>
                {/* <div className="section-proj card-grid"> */}
                    <div className="my-proj card-grid" >
                    <div className="make-box card-grid">Make-Eat<a href="https://make-eat.herokuapp.com/#/"><img className="proj-gif" src={MakeEat} alt="MakeEat" /></a>
                    <div className="view-code">
                    <Tooltip TransitionComponent={Zoom} title="View App">
                        <a href="https://make-eat.herokuapp.com/#/"><VisibilityIcon/></a>
                        </Tooltip>
                        <Tooltip TransitionComponent={Zoom} title="View Code">
                        <a href="https://github.com/LiorGob/make-eat"><CodeIcon style={{marginLeft:"10px"}}/></a>
                        </Tooltip>  
                        </div>
                    <p>Recipes Marketplace (React, Node, MongoDB)</p>
                    </div>
                        <div className="appsus-box card-grid">Appsus<a href="https://github.com/LiorGob/AppSus"><img className="proj-gif" src={Appsus} alt="Appsus" /></a>
                        <div className="view-code">
                        <Tooltip TransitionComponent={Zoom} title="View App">
                        <a href="https://liorgob.github.io/AppSus/#/"><VisibilityIcon/></a>
                        </Tooltip> 
                        <Tooltip TransitionComponent={Zoom} title="View Code">
                        <a href="https://github.com/LiorGob/AppSus"><CodeIcon style={{marginLeft:"10px"}}/></a>
                        </Tooltip> 
                        </div>
                            <p>AppSus is a demo-like version of GoogleNotes and Gmail.</p>
                        </div>
                        <div className="meme-box card-grid">Meme<a href="https://github.com/shirlyavivzemach/memegene"><img className="proj-gif" src={Meme} alt="Meme" />
                        </a>
                        <div className="view-code">
                        <Tooltip TransitionComponent={Zoom} title="View App">
                        <a href="https://shirlyavivzemach.github.io/memegene/"><VisibilityIcon/></a>
                        </Tooltip> 
                        <Tooltip TransitionComponent={Zoom} title="View Code">
                        <a href="https://github.com/shirlyavivzemach/memegene"><CodeIcon style={{marginLeft:"10px"}}/></a>
                        </Tooltip>
                        </div>
                        <p>Meme-It's a free online image maker that allows you to add custom resizable text to images.</p>

                        </div>
                    </div>
                </div>
            // </div>
        )
    }
}
