import React from 'react'
import Resume from '../assets/files/CVShirlyAvivZemach.pdf'
import { Link, animateScroll as scroll} from 'react-scroll';
import GitHubIcon from '@material-ui/icons/GitHub';

export function Nav() {
 

    return (
        <header className="main-header flex align-center space-between">
            <div className="logo"><a href="https://github.com/shirlyavivzemach">< GitHubIcon/></a></div>
            <ul className="main-nav flex space-between">
                <Link activeClass="active"
                    to="about"
                    spy={true}
                    smooth={true}
                    offset={-30}
                    duration={500}><li className="link flex align-center">About</li>
                </Link>
                <Link activeClass="active"
                    to="tech"
                    spy={true}
                    smooth={true}
                    offset={-29}
                    duration={500}><li className="link flex align-center">Tech</li>
                </Link>
                {/* <li className="link flex align-center">Education</li> */}
                <Link activeClass="active"
                    to="proj"
                    spy={true}
                    smooth={true}
                    offset={-30}
                    duration={500}><li className="link flex align-center">Projects</li>
                </Link>
               
                <a href={Resume} download><li className="link flex align-center">Resume</li></a>
            </ul>
        </header>
    )
}
