import React from 'react'
import ReactLogo from '../assets/imgs/react.png'
import Angular from '../assets/imgs/angular.png'
import Vue from '../assets/imgs/vue.png'
import Css from '../assets/imgs/css.png'
import SASS from '../assets/imgs/sass.png'
import JS from '../assets/imgs/js.png'
import Node from '../assets/imgs/node.png'
import Github from '../assets/imgs/github.png'
import Html5 from '../assets/imgs/html5.png'
import Jquery from '../assets/imgs/jquery.png'
import Mongo from '../assets/imgs/mongodb.png'
import Firebase from '../assets/imgs/firebase.png'

export function Tech() {

    return (
        <div className="tech-section flex column main-container"  id="tech">
            <h2 className="tech-title">Technologics</h2>
            <ul className="tech clean-list slide-fwd-center ">
            <li >JS <img src={JS} alt="JS"/></li>
            <li>Html 5<img src={Html5} alt="Html5"/></li>
            <li>CSS <img src={Css} alt="Css"/></li>
            <li>jQuery<img src={Jquery} alt="Jquery"/></li>
            <li>React <img src={ReactLogo} alt="ReactLogo"/></li>
            <li>Vue <img src={Vue} alt="Vue"/></li>
            <li>Angular <img src={Angular} alt="Angular"/></li>
            <li>SASS<img src={SASS} alt="SASS"/></li>
            <li>node <img src={Node} alt="Node"/></li>
            <li>mongoDB <img src={Mongo} alt="Mongo"/></li>
            <li>Firebase <img src={Firebase} alt="Firebase"/></li>
            <li>Github <img src={Github} alt="Github"/></li>
            </ul>
          
        </div>
    )
}
