import React from 'react'
import ProfilePic from '../assets/imgs/shirly.jpg'
import StarIcon from '@material-ui/icons/Star';
import MaximizeIcon from '@material-ui/icons/Maximize';
export function Hero() {
    return (
        <section className="hero flex">
            <div className="portfolio-card flex align-center ">
                <div className="column text-center">
                    <img className="profile-pic" src={ProfilePic} alt="ProfilePic" />

                    <h3 className="title">Shirly Aviv Zemach</h3>

                    {/* <p><StarIcon /></p> */}
                    <div className="desc flex">
                        <div className="stars flex">
                            Full Stack Developer<span><StarIcon />
                            </span>Front End Developer<span>
                                <StarIcon /></span>Web Developer
                            </div>
                    </div>

                </div>

            </div>
        </section>
    )
}
